.UserBox{
    display: flex;
    padding-bottom: 10px;
    width: 70%;
}

.selectedPage{
    font-weight: bold;
}

.AvaAndButton{
    width: 15%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}


.Avatar>img{
    width: 70px;
    height: 70px;
    border-radius: 50%;
}

.UserInfo{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    border: 2px solid skyblue;
    border-radius: 10px;
}

.UserInfoTop{
    display: flex;
    justify-content: space-between;
}
.UserInfoBottom{
    display: flex;
    justify-content: space-between;
}